<template>
	<ul class="mes" v-if="getUser.allMes.length>0">
		<li v-for="umeg in getUser.allMes">
			<div>
				<img :src="umeg.author.avatar_url" alt="">
				<p class="basemes">{{umeg.author.loginname}} <span class="time">{{umeg.create_at | getTime}}</span></p>
				<p class="reply"><span v-if="umeg.type=='reply'" class="type">回复了您的话题</span><span v-if="umeg.type=='at'" class="type">在回复中@了您</span><span class="sign" v-if="!umeg.has_read"></span></p>
			</div>
		</li>
	</ul>
	<div v-if="getUser.allMes.length==0" class="noMes">
		没有信息
	</div>
</template>
<script>
	import {getAllMeg} from '../vuex/action'
	export default{
		vuex:{
			getters:{
				getUser:({userMes})=>userMes.user,
			},
			actions:{
				getAllMeg
			}	
		},
		route:{
			data(transition){
				this.getAllMeg(this.getUser.accesstoken);
			}
		},
	}
</script>
<style>
	.mes{background-color: #fff;padding:0;}
	.mes li{list-style:none;overflow: hidden;padding:10px 20px;border-bottom:1px solid #eaeaea;}
	.mes li img{width:15%;border-radius:50%;float:left;margin-right:10px;}
	.mes li p{float:left;margin:0;width:80%;}
	.mes li p.basemes{color:#323232;font-size: 1rem;line-height: 1.3em}
	.mes li p.basemes .time{color:#80BD01;float:right;font-size: 0.8rem}
	.mes li .reply .type{color:#767676;line-height: 3em;font-size: 0.8rem}
	.mes li .reply .sign{width:10px;height: 10px;background-color: #8dbd01;border-radius: 5px;display: inline-block;float:right;margin-top:10px;}
	.noMes{background-color: #fff;height: 200px;line-height: 200px;text-align: center;color:#999;font-size: 1.5em;margin-top:10px;}
</style>


